import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

const galleryEl = document.querySelector('.js-gallery');

let markup = '';
for (const img of images) {
    const { preview, original, description } = img;
    markup += `<li class="gallery-item">
                        <a class="gallery-link" href="${original}">
                            <img
                            class="gallery-image"
                            src="${preview}"
                            data-source="${original}"
                            alt="${description}"
                            />
                        </a>
                    </li>`;
}
galleryEl.innerHTML = markup;

const galleryOption = {
  captionsData: 'alt', // Use the image's alt attribute as the caption
  captionDelay: 250, // Delay of 250ms before showing the caption
};

new SimpleLightbox('.gallery a', galleryOption);

